@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,700&display=swap');

body {    
    background: no-repeat url('./images/icons/tyre.png');
    font-family: Open Sans;
    font-size: 16px;    
    background-color: #ffffff;
}
#root {    
    margin: 0 auto;
    max-width: 600px;
}
#menu {
    text-align: center;
    margin: 10px 10px;
}
#addcar, #additem, #editcar{
    display: flex;
    flex-direction: column;
    text-align: center;
}
label {
    display: flex;
    justify-content: space-between;
    margin: 10px 10px;
}
.radio {
    display: flex;
    justify-content: space-between;
    width: 200px;
    margin: 10px auto;
}
.reportList, .reportheadercols {
    display: flex;
    flex-direction: row;
}
.reportheadercols {
    font-weight: 700;
}
.reportList div, .reportheadercols div{
    width: 85px;
    height: 25px;
    font-size: 14px;
}
.reportList button img {
    width: 20px;
}
.reportList div:last-child, .reportheadercols div:last-child{
    width: 35px
}
input[type="text"], input[type="number"], input[type="date"], select, input[type="range"] {
    border-radius: 8px;
    width: 200px;
    height: 30px;
    border: 1px solid rgb(17, 0, 255);
    box-sizing:border-box;
    color: #000000;
    background-color: #ffffff;
    padding: 0 10px 0 10px;
    font-size: 16px;
}
button {
    font-size: 16px;
    background-color: rgb(17, 0, 255);
    color: #ffffff;    
    border: none;
    border-radius: 8px;
}
#addcar button, #additem button, #editcar button {
    width: 200px;
    height: 40px;
    margin: 10px auto;
    
}
button:disabled,
button[disabled] {
    color: rgb(95, 94, 94);
    background-color: rgb(17, 0, 255, 0.5);
}
button:hover {
    cursor: pointer;
}
#categorylist {
    display: block
}
#categorylist label {
    display: inline-block;
    text-align: center;
    font-size: 14px;
    margin: 5px 5px;
}
#categorylist label input {
    width: 13px;
    height: 13px;
}
#selectcar {
    display: flex;
    width: 300px;
    height: 30px;
    margin: 0 auto;
}
#selectcar button, .reportList button {
    background: none;
    width: 30px;
    border: none;
    padding: 0;
}
#selectcar button img {
    width: 30px;    
}
#selectcar button:hover{    
    cursor: pointer;
}
#selectcar a img{
    width: 30px;
}
#menu img{
    height: 40px;
    margin: 0 15px;
}
#error {
    color: red;
    height: 25px;
}
#fuelconsumption {
    display: block;
}
.fuelconsumptionrow {
    display: flex;    
}
.reportheader {
    font-weight: 700;
    margin-top: 10px;
}

input[type=range] {
    -webkit-appearance: none;
    width: 200px;
}
input[type=range]:focus {
outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
width: 100%;
height: 9.5px;
cursor: pointer;
box-shadow: 1.3px 1.3px 1px #000000, 0px 0px 1.3px #0d0d0d;
background: #2d71a9;
border-radius: 3.1px;
border: 0.7px solid #010101;
}
input[type=range]::-webkit-slider-thumb {
box-shadow: 1.1px 1.1px 1.4px #000000, 0px 0px 1.1px #0d0d0d;
border: 1.5px solid #000000;
height: 28px;
width: 19px;
border-radius: 7px;
background: #ffffff;
cursor: pointer;
-webkit-appearance: none;
margin-top: -9.95px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
background: #4690ce;
}
input[type=range]::-moz-range-track {
width: 100%;
height: 9.5px;
cursor: pointer;
box-shadow: 1.3px 1.3px 1px #000000, 0px 0px 1.3px #0d0d0d;
background: #2d71a9;
border-radius: 3.1px;
border: 0.7px solid #010101;
}
input[type=range]::-moz-range-thumb {
box-shadow: 1.1px 1.1px 1.4px #000000, 0px 0px 1.1px #0d0d0d;
border: 1.5px solid #000000;
height: 28px;
width: 19px;
border-radius: 7px;
background: #ffffff;
cursor: pointer;
}
input[type=range]::-ms-track {
width: 100%;
height: 9.5px;
cursor: pointer;
background: transparent;
border-color: transparent;
color: transparent;
}
input[type=range]::-ms-fill-lower {
background: #205179;
border: 0.7px solid #010101;
border-radius: 6.2px;
box-shadow: 1.3px 1.3px 1px #000000, 0px 0px 1.3px #0d0d0d;
}
input[type=range]::-ms-fill-upper {
background: #2d71a9;
border: 0.7px solid #010101;
border-radius: 6.2px;
box-shadow: 1.3px 1.3px 1px #000000, 0px 0px 1.3px #0d0d0d;
}
input[type=range]::-ms-thumb {
box-shadow: 1.1px 1.1px 1.4px #000000, 0px 0px 1.1px #0d0d0d;
border: 1.5px solid #000000;
height: 28px;
width: 19px;
border-radius: 7px;
background: #ffffff;
cursor: pointer;
height: 9.5px;
}
input[type=range]:focus::-ms-fill-lower {
background: #2d71a9;
}
input[type=range]:focus::-ms-fill-upper {
background: #4690ce;
}

img:hover {
    margin-top: -5px;
    transition: 0.5s;
}
#period {
    display: flex;
    justify-content: center;
    margin: 10px 10px;
}
#period input {
    margin: 0 15px 0 5px;
}
#fuelconsumption {
    position: absolute;
    top: 0;
    left: calc(50% - 182px);    
}
#fuelconsumption div {
    display: flex;
    justify-content: center;
}
#refueling, #other-costs {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-top: 40px;
}
